.popup {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 15;
  background-color: rgba(0, 0, 0, 0.85);
  text-align: center;
  font-size: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s;
}

.popup:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup.is-active {
  visibility: visible;
  opacity: 1;
}

.popup.is-active .popup__inner {
  transform: scale(1);
}

.popup__inner {
  display: inline-block;
  vertical-align: middle;
  font-size: var(--fz);
  background-color: #fff;
  position: relative;
  max-width: 335px;
  padding-bottom: 40px;
  transition: transform 0.3s;
  transform: scale(0);
}

.popup__close {
  display: inline-block;
  top: 15px;
  right: 15px;
  position: relative;
  display: inline-block;
  width: 15px;
  height: 15px;
  position: absolute;
}

.popup__close::before, .popup__close::after {
  position: absolute;
  content: '';
  width: 1px;
  height: 100%;
  background-color: #2f2f2f;
  top: 0;
  left: 50%;
}

.popup__close::before {
  transform: rotate(45deg);
}

.popup__close::after {
  transform: rotate(-45deg);
}

.popup__logo {
  margin-bottom: 30px;
  background-color: #f0f5f4;
  padding-top: 25px;
  padding-bottom: 25px;
